      .div-item-wallet {
        display: flex;
        width: 100%;
        margin: 0;
        border: none;
        border-bottom: 0.5px solid #8e8e8e;
        padding: 5px;

        .text-name-wallet {
          font-family: "Roboto";
          font-style: normal;
