          font-style: normal;
          font-weight: 500;
          font-size: 16px;
          color: #fb942e;
          text-decoration: none;
          margin: 0;
        }

        .text-money-gift {
          font-family: "Roboto";
          font-style: normal;
          font-weight: 500;
          font-size: 16px;
          color: #fb422e;
          text-decoration: none;
          margin: 0;
        }
      }
      .div-item-wallet-last {
        display: flex;
        justify-content: space-between;
        width: 100%;
        padding: 5px;

        .text-name-wallet {
          font-family: "Roboto";
          font-style: normal;
          font-weight: 500;
          font-size: 16px;
          color: #727272;
          text-decoration: none;
          margin: 0;
        }

        .text-money-paypoint {
          font-family: "Roboto";
          font-style: normal;
          font-weight: 500;
          font-size: 16px;
          color: #d931ff;
          text-decoration: none;
          margin: 0;
        }
      }
    }
  }
}
