.login-affiliate {
    position: relative;
    width: 100%;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #4158D0;
    background-image: linear-gradient(43deg, #3E187A 0%, #C850C0 46%, #FFCC70 100%);
    // box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px, rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
    // background: url('../../../assets/images/bg.png');
    // background-size: cover;

    // Khong trng ( lm phn gn sng to hn)
    .login-affiliate__blank {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 50px;
        background-color: #994ECC;
    }

    .login-affiliate__wave {
        position: absolute;
        width: 100%;
        height: 195px;
        bottom: 0px;
        left: 0;
        background: url('../../../assets/images/wave.svg');
        animation: wave 8s linear infinite;
        // background-position: 200px
    }

    .login-affiliate__wave::before {
        content: '';
        position: absolute;
        width: 100%;
        height: 195px;
        bottom: 0px;
        left: 0;
        background: url('../../../assets/images/wave.svg');
        opacity: .2;
        // background-position: 300px
        animation: wave-reverse 8s linear infinite;

    }

    .login-affiliate__wave::after {
        content: '';
        position: absolute;
        width: 100%;
        height: 195px;
        bottom: 0px;
        left: 0;
        background: url('../../../assets/images/wave.svg');
        opacity: .8;
        animation-delay: -4s;
        // background-position: 600px
        animation: wave 16s linear infinite;
    }

    @keyframes wave {
        0% {
            background-position: 0;
        }

        100% {
            background-position: 1280px;
        }
    }

    @keyframes wave-reverse {
        0% {
            background-position: 1280px;
        }

        100% {
            background-position: 0;
        }
    }

    .login-affiliate__form {
        display: flex;
        // margin-top: 210px;
        // padding: 16px;
        background-color: $colorMoreLightViolet;
        width: 600px;
        z-index: 1;
        height: 400px;
        border-radius: $borderRadiusL;
        box-shadow: rgba(0, 0, 0, 0.16) 0px 3px 6px, rgba(0, 0, 0, 0.23) 0px 3px 6px;

        .login-affiliate__form--left {
            display: flex;
            align-items: center;
            justify-content: center;
            padding: 16px;
            background-color: #4158D0;
            // background-image: linear-gradient(43deg, #3E187A 0%, #C850C0 46%, #FFCC70 100%);
            background-image: linear-gradient(43deg, #C850C0 46%, #3E187A 100%);

            height: 100%;
            width: 50%;
            border-top-left-radius: $borderRadiusL;
            border-bottom-left-radius: $borderRadiusL;
            // border-right: 1px solid black;

            .login-affiliate__form--left-child {
                background-color: transparent;
                border-radius: $borderRadiusL;
            }
        }

        .login-affiliate__form--right {
            display: flex;
            flex-direction: column;
            align-items: center;
            // gap: $gapL;
            background-color: $colorWhite;
            height: 100%;
            width: 50%;
            border-top-right-radius: $borderRadiusL;
            border-bottom-right-radius: $borderRadiusL;
            padding: 12px;
            overflow-y: hidden;

            .login-affiliate__form--right-logo {
                width: 80px;
                height: 50px;
                border-radius: $borderRadiusFull;
            }

            .login-affiliate__form--right-login {
                display: flex;
                flex-direction: column;
                width: 100%;
                height: 100%;
                align-items: center;
                justify-content: center;
                gap: $gapL;
                opacity: 1;
                transition: all 0.5s ease-out;
                padding-top: 12px;

                &.hide {
                    visibility: hidden;
                    height: 0px;
                    opacity: 0;
                }

                .login-affiliate__form--right-login-header {
                    text-align: center;

                    span {
                        font-size: $textFontSizeXL;
                        font-weight: $fontWeightXL;
                        color: $colorBlack;
                    }

                }

                .login-affiliate__form--right-login-input {
                    display: flex;
                    flex-direction: column;
                    gap: $gapL;
                    width: 100%;

                }

                .login-affiliate__form--right-login-line {
                    position: relative;
                    width: 70%;
                    height: 2px;
                    background-color: $colorMoreLightGray;
                    border-radius: $borderRadiusL;
                    margin: 0 auto;

                    .login-affiliate__form--right-login-line-other {
                        position: absolute;
                        top: 50%;
                        left: 50%;
                        transform: translate(-50%, -50%);
                        color: $colorLightGray;
                        background-color: $colorWhite; // t mu nn  ch khng b che bi ng thng
                        padding: 0 8px; // To khong cch gia ch v vin nn
                    }
                }

                .login-affiliate__form--right-login-sign-up {
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    gap: $gapXS;

                    .login-affiliate__form--right-login-sign-up-label {
                        font-size: $textFontSizeM;
                        font-weight: $fontWeightL;
                        color: $colorBlack;

                        &.high-light {
                            font-weight: $fontWeightXL;
                            color: $colorDarkViolet;
                        }
                    }
                }
            }

            .login-affiliate__form--right-sign-up {
                display: flex;
                flex-direction: column;
                width: 100%;
                height: 100%;
                align-items: center;
                justify-content: center;
                gap: $gapL;
                opacity: 1;
                transition: all 0.5s ease-out;

                &.hide {
                    visibility: hidden;
                    height: 0px;
                    opacity: 0;
                }

                .login-affiliate__form--right-sign-up-header {
                    text-align: center;

                    span {
                        font-size: $textFontSizeXL;
                        font-weight: $fontWeightXL;
                        color: $colorBlack;
                    }

                }

                .login-affiliate__form--right-sign-up-input {
                    display: flex;
                    flex-direction: column;
                    gap: $gapL;
                    width: 100%;
                }

                .login-affiliate__form--right-sign-up-line {
                    position: relative;
                    width: 70%;
                    height: 2px;
                    background-color: $colorMoreLightGray;
                    border-radius: $borderRadiusL;
                    margin: 0 auto;

                    .login-affiliate__form--right-sign-up-line-other {
                        position: absolute;
                        top: 50%;
                        left: 50%;
                        transform: translate(-50%, -50%);
                        color: $colorLightGray;
                        background-color: $colorWhite; // t mu nn  ch khng b che bi ng thng
                        padding: 0 8px; // To khong cch gia ch v vin nn
                    }
                }

                .login-affiliate__form--right-sign-up-sign-up {
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    gap: $gapXS;

                    .login-affiliate__form--right-sign-up-sign-up-label {
                        font-size: $textFontSizeM;
                        font-weight: $fontWeightL;
                        color: $colorBlack;

                        &.high-light {
                            font-weight: $fontWeightXL;
                            color: $colorDarkViolet;
                        }
                    }
                }
            }
        }

    }
}

