.login-affiliate__button {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 8px;
    height: fit-content;
    width: 100%;
    background-color: $colorWhite;
    border: 1px solid $colorDarkViolet;
    border-radius: $borderRadiusFull;
    text-align: center;
    transition: all 0.3s ease-out;

    span {
        font-size: $textFontSizeM;
        font-weight: $fontWeightXL;
        color: $colorDarkViolet;
    }

    &:hover {
        background-color: $colorDarkViolet;

        span {
            color: $colorWhite;
        }
    }
}

// Modal nhp m OTP
.login-affiliate__card--information-otp {
    display: flex;
    flex-direction: column;
    gap: $gapM;

    .login-affiliate__card--information-otp-header {
        display: flex;
        align-items: center;
        justify-content: center;
        text-align: center;
        padding: 12px 0px 0px 0px;

        span {
            font-size: $textFontSizeXL;
            font-weight: $fontWeightXL;
            color: $colorBlack;
        }
    }

    .login-affiliate__card--information-otp-label {
        text-align: center;
        font-size: $textFontSizeM;
        font-weight: $fontWeightL;
        color: $colorBlack;

        .bold {
            font-weight: $fontWeightXL;
        }

        .high-light {
            color: $colorOrange;
        }

        .click-able {
            cursor: pointer;
        }
    }

    .login-affiliate__card--information-forgot-password {
        font-size: $textFontSizeM;
        font-weight: $fontWeightL;
        color: $colorBlack;
        text-align: center;

        &.high-light {
            color: $colorOrange;
            font-weight: $fontWeightXL;
        }

        &.clickable {
            cursor: pointer;
        }

    }


    .login-affiliate__card--information-otp-digit {
        display: flex;
        gap: $gapL;
        justify-content: center;
        align-items: center;

        .login-affiliate__card--information-otp-digit-number {
            width: 55px;
            height: 55px;
            font-size: $textFontSizeXL;
            text-align: center;
            border: 3px solid $colorMoreLightGray;
            border-radius: 8px;
            outline: none;
            transition: all 0.3s ease;

            &:focus {
                border-color: $colorViolet;
            }
        }
    }

    .login-affiliate__card--information-otp-resend {}
}

.login-affiliate__card--information-password {
    display: flex;
    flex-direction: column;
    gap: $gapM;
    padding: 12px 12px 0px 12px;

    .login-affiliate__card--information-password-header {
        display: flex;
        align-items: center;
        justify-content: center;
        text-align: center;
        padding: 12px 0px 0px 0px;

        span {
            font-size: $textFontSizeXL;
            font-weight: $fontWeightXL;
            color: $colorBlack;
        }
    }

    .login-affiliate__card--information-password-process-bar {
        display: flex;
        position: relative;
        width: 100%;
        height: 8px;
        border-radius: $borderRadiusFull;
        background-color: $colorExtraLightGray;

        .login-affiliate__card--information-password-process-bar-child {
            position: absolute;
            top: 0;
            left: 0;
            background-color: $colorBlack;
            width: 0%;
            height: 100%;
            transition: all 0.5s ease-out;
            border-radius: $borderRadiusFull;

            &.empty {
                background-color: $colorMoreLightGray;
                width: 0%;
            }

            &.week {
                background-color: $colorRed;
                width: 25%;

            }

            &.fear {
                background-color: $colorOrange;
                width: 50%;
            }

            &.good {
                background-color: $colorLime;
                width: 75%;
            }

            &.strong {
                background-color: $colorGreen;
                width: 100%;
            }
        }
    }

    .login-affiliate__card--information-password-label {
        display: flex;
        justify-content: space-between;

        .login-affiliate__card--information-password-label-child {
            font-size: $textFontSizeM;
            font: $fontWeightL;
            color: $colorWhite;

            &.empty {
                color: $colorLightGray;
            }

            &.week {
                color: $colorRed;
            }

            &.fear {
                color: $colorOrange;
            }

            &.good {
                color: $colorLime;
            }

            &.strong {
                color: $colorGreen;
            }
        }
    }

    .login-affiliate__card--information-password-condition-required {
        display: flex;
        flex-direction: column;
        gap: $gapS;

        .login-affiliate__card--information-password-condition-required-child {
            display: flex;
            gap: $gapXS;
            align-items: center;
            transition: all 0.5s ease-out;

            span {
                font-size: $textFontSizeM;
                font-weight: $fontWeightL;
                color: $colorLightGray;
            }

            &.checked {

                span {
                    font-size: $textFontSizeM;
                    font-weight: $fontWeightL;
                    color: $colorGreen;
                }
            }
        }
    }
}